<template>
  <div class="testPage">
    <div id="map" style="height: 100%"></div>
    <div style="position: absolute;z-index: 10;bottom: 20px;left: 20px;padding: 10px;background: white;width: 100px;text-align: left">
      <img src="../../assets/pinggu/legend2.png" alt="">
      <span style="position: absolute;top: 16px;left: 36px">极重要</span>
      <span style="position: absolute;bottom: 16px;left: 36px">不重要</span>
    </div>
  </div>
</template>
<script>
import arcgisPackage from '@/utils/arcgisTools'
import config from '../../setting/mapConfig'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, sensitivity } from '@/utils/layers'
export default {
  name: 'TestPage',
  data () {
    return {}
  },
  mounted () {
    this.createMap()
  },
  methods: {
    // 生成地图的函数
    async createMap () {
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 脆弱评估
      let vulnerabilitylayer = await sensitivity().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, vulnerabilitylayer, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })

      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, vulnerabilitylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'map',
        center: config.centerPoint,
        zoom: 7
      })
    }
  }
}
</script>
<style scoped lang="scss">
  .testPage{
  }
</style>
